import service from "auth/FetchInterceptor";
import { CURRENTLY_ACTIVE } from "constants/AllocationChangeReportConstant";
import API_CONSTANTS from "constants/ApiConstant";
import { HEADERS_NAME_FOR_EXPORT } from "constants/CommonConstants";
import moment from "moment";
import Utils from "utils";
import { DATE_FORMAT_DD_MM_YYYY } from "constants/DateConstant";

const emptyValue = "_";

const allocationChangeReport = {};

allocationChangeReport.preparedDataForExport = (data = [], filterVisibleColumns, forExcel = true) => {
  const _data = [];
  let headers = {};
  data.forEach(item => {
    const values = {
      employeeId: item?.employeeId || "-",
      hrmsId: item?.hrmsId || "-",
      employeeName: item?.employeeName || "-",
      employeeCountry: item.employeeCountry || "-",
      employeeLocation: item?.employeeLocation || "-",
      employeeDateOfJoining: item?.employeeDateOfJoining || "-",
      currentParentAccount: item?.currentParentAccount || "-",
      reservedForAccountName: item?.reservedForAccountName || "-",
      projectName: item?.projectName || "-",
      domainName: item?.domainName || "-",
      subDomainName: item?.subDomainName || "-",
      employeeTotalExperience: item?.employeeTotalExperience || "-",
      employeeMobileNumber: item?.employeeMobileNumber || "-",
      employeeDesignation: item?.employeeDesignation || "-",
      employeeEmail: item?.employeeEmail || "-",
      functionalGroupName: item?.functionalGroupName || "-",
      subStatus: item?.subStatus || "_",
      subStatusAging: item?.subStatusAging || "-",
      subStatusStartDate: item?.subStatusStartDate || "-",
      subStatusEndDate: item?.subStatusEndDate || "-",
      cclName: item?.cclName || "-",
      cclEmail: item?.cclEmail || "-",
      employeeRole: item?.employeeRole || "-",
      onBehalfOf: item?.onBehalfOf || "-",
      employeeStatus: item?.employeeStatus || "-",
      employeeInactiveStatusDate: item?.employeeInactiveStatusDate || "-",
    };
    if (filterVisibleColumns) {
      Object.keys(values).forEach(itemKey => {
        if (!filterVisibleColumns[itemKey]) {
          delete values[itemKey];
        }
      });
    }
    if (item.allocations) {
      item.allocations.forEach(i => {
        values["parentAccount"] = i?.parentAccount || emptyValue;
        values["allocationAccount"] = i?.allocationAccount || emptyValue;
        values["allocationStatus"] = i?.allocationStatus || emptyValue;
        values["allocationStartDate"] = i?.allocationStartDate || emptyValue;
        values["allocationEndDate"] = i?.allocationEndDate || CURRENTLY_ACTIVE;
        values["allocationAging"] = i?.allocationAging || emptyValue;
        _data.push({ ...values });
      });
    } else {
      _data.push(values);
    }
    if (forExcel) headers = Utils.makeHeaderDataForExcel(values);
  });
  if (forExcel) _data.unshift(headers);
  return _data;
};

allocationChangeReport.preparedHeadersForAllPDF = (data = []) =>
  Object.keys(data[0]).map(key => {
    return { key, value: HEADERS_NAME_FOR_EXPORT[key] };
  });

allocationChangeReport.preparedHeadersForVisibleColumnPDF = (data = []) =>
  data.map(col => {
    return { key: col.dataIndex, value: HEADERS_NAME_FOR_EXPORT[col.dataIndex] };
  });

allocationChangeReport.flatResponse = items => {
  let values = { data: [], nestedData: [] };
  items.forEach(item => {
    const data = {
      hrmsId: item?.hrmsId || "",
      employeeId: item?.employeeId || "",
      employeeName: item?.employeeName || "",
      employeeCountry: item.employeeCountry || "",
      employeeEmail: item?.employeeEmail || "",
      employeeMobileNumber: item?.employeeMobileNumber || "",
      employeeLocation: item?.employeeLocation || "",
      employeeRole: item?.employeeRole || "",
      employeeDesignation: item?.employeeDesignation || "",
      employeeTotalExperience: item?.employeeTotalExperience || "",
      employeeStatus: item?.employeeStatus || "",
      employeeDateOfJoining: item?.employeeDateOfJoining
        ? moment(item.employeeDateOfJoining).format(DATE_FORMAT_DD_MM_YYYY)
        : "",
      functionalGroupName: item?.functionalGroupName || "",
      cclName: item?.cclName || "",
      cclEmail: item?.cclEmail || "",
      subStatus: item?.subStatus || "",
      subStatusStartDate: item?.subStatusStartDate
        ? moment(item.subStatusStartDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "",
      subStatusEndDate: item?.subStatusEndDate ? moment(item.subStatusEndDate).format(DATE_FORMAT_DD_MM_YYYY) : "",
      subStatusAging: item?.subStatusAging || "",
      allocationStatus: item?.allocationStatus || "",
      currentParentAccount: item?.currentParentAccount || "",
      projectName: item?.projectName || "",
      domainName: item?.domainName || "",
      subDomainName: item?.subDomainName || "",
      reservedForAccountName: item?.reservedForAccountName || "",
      employeeInactiveStatusDate: item?.employeeInactiveStatusDate
        ? moment(item?.employeeInactiveStatusDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "",
      onBehalfOf: item?.onBehalfOf || "",
      notes: item?.notes || "",
    };
    const nestedData = item?.allocations.map(i => {
      return {
        parentAccount: i?.parentAccount || "",
        allocationAccount: i?.allocationAccount || "",
        allocationStatus: i?.allocationStatus || "",
        allocationStartDate: i?.allocationStartDate ? moment(i.allocationStartDate).format(DATE_FORMAT_DD_MM_YYYY) : "",
        allocationEndDate: i?.allocationEndDate
          ? moment(i.allocationEndDate).format(DATE_FORMAT_DD_MM_YYYY)
          : CURRENTLY_ACTIVE,
        allocationAging: i?.allocationAging || "",
        employeeId: item.employeeId,
        notes: i?.notes || "",
      };
    });
    data["allocations"] = nestedData;
    values.data.push(data);
    values.nestedData.push(...nestedData);
  });
  return values;
};

allocationChangeReport.prepareData = items => {
  let values = { data: [], nestedData: [] };
  items.map(record => {
    let item = Utils.prepareNestedDataForReports(record);
    const nestedData = item?.allocations?.map(nestedRecord => {
      let nestedItem = Utils.prepareNestedDataForReports(nestedRecord);
      return nestedItem;
    });
    item["allocations"] = nestedData;
    values.data.push(item);
    values.nestedData.push(...nestedData);
  });

  return values;
};
allocationChangeReport.getReport = async data => {
  const { path, method } = API_CONSTANTS.GET_ALLOCATION_CHANGE_REPORT;
  try {
    const response = await service({
      url: path,
      method,
      data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in getting the employee list.", e);
  }
};

export default allocationChangeReport;
